* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

@import url('https://fonts.googleapis.com/css?family=Open+Sans:@400,600');

.container {
	max-width: 1325px;
	margin: 20px auto;
	padding: 0 15px;
}

.list {
	font-family: 'Open Sans', sans-serif;

	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 15px;

	@media (max-width: 1130px) {
		grid-template-columns: 1fr 1fr;
		justify-content: center;
	}
	@media (max-width: 700px) {
		grid-template-columns: 1fr;
		justify-content: center;
	}
}



.column-1 {
	display: grid;
	grid-template-columns: 1fr;
	gap: 15px;
	@media (min-width: 1130px) {
		grid-row: 1 / 4;
	}
}

.column-3 {
	display: grid;
	grid-template-columns: 1fr;
	gap: 15px;
	@media (min-width: 1130px) {
		grid-column: 3 / 4;
		grid-row: 1 / 4;
	}
	@media (min-width: 700px) {
		grid-column: 1 / 3;
	}
}


.card-item {
	padding: 35px 25px 80px 35px;
	transition: all .3s;
	position: relative;
	&::before {
	    position: absolute;
	    top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		background-color: #251F1E;
		content: '';
		transition: all .3s;
	}

	// 1 карточка
	.column-1 &:nth-child(1) {
		background: url("../img/сard/1.jpg") center center/cover no-repeat;
	}

	// 2 карточка
	.column-1 &:nth-child(2) {
		background: url("../img/сard/2.jpg") center center/cover no-repeat;
	}

	// 3 карточка
	.list > &:nth-child(2) {
		background: url("../img/сard/3.jpg") center center/cover no-repeat;
		padding-bottom: 0;
		@media (max-width: 840px) {
			padding-bottom: 80px;
		}
	}

	// 4 карточка
	.list > &:nth-child(3) {
		background: url("../img/сard/4.jpg") center center/cover no-repeat;
	}

	// 5 карточка
	.list > &:nth-child(4) {
		background: url("../img/сard/5.jpg") center center/cover no-repeat;
	}

	// 6 карточка
	.column-3 &:nth-child(1) {
		background: url("../img/сard/6.jpg") center center/cover no-repeat;
	}

	// 7 карточка
	.column-3 &:nth-child(2) {
		background: url("../img/сard/7.jpg") center center/cover no-repeat;
		ul {
			margin-bottom: 130px;
		}
	}


	ul {
		margin-bottom: 50px;
		position: relative;
	}

	li {
		list-style: none;
		display: flex;
		position: relative;
		&::before {
			display: inline-block;
			margin-top: 5px;
			margin-right: 12px;
			min-width: 12px;
			height: 12px;
			background-color: #fff;
			content: '';
		}
	}

	a {
		position: relative;
		color: #fff;
		text-decoration: none;
		display: block;
		transition: all .6s;
		&:hover {
			opacity: 1;
			text-decoration: underline;
		}
	}

	&:hover {
		&::before {
			opacity: 0.5;
		}
	}

	&__title {
		font-weight: 600;
		font-size: 21px;
		line-height: 29px;
		margin-bottom: 40px;
	}

	&__link {
		font-weight: 400;
		font-size: 17px;
		line-height: 23px;
		margin-bottom: 20px;
	}

	.btn {
		position: relative;
		display: block;
		font-weight: 600;
		font-size: 17px;
		line-height: 23px;
		text-align: center;
		width: 265px;
		height: 69px;
		margin: 0 auto;
		transition: all .6s;
		cursor: pointer;
		&:hover {
			opacity: .6;
		}
		@media (max-width: 840px) {
			width: 200px;
		}
	}

	.btn:last-child {
		margin-top: 16px;
		background-color: transparent;
		border: 2px solid #fff;
		color: #fff;
	}


}
